import Global from '../../store/Global.js';
import { mapState, mapMutations } from 'vuex';
import mInput from '../../components/m-input.vue';

export default {
	components: {
		mInput,
		Global
	},
	data() {
		return {
			providerList: [],
			hasProvider: true,
			account: '',
			password: '',
			positionTop: 0,
			sysname: Global.sysname
		};
	},
	computed: mapState(['forcedLogin']),
	methods: {
		...mapMutations(['login']),
		bindLogin() {
			var self = this;		
			uni.showLoading({
				title: '正在登录'
			});
			const result = service.loginFromNet(this.account, this.password, function() {
				uni.showToast({
					icon: 'none',
					title: '登录成功'
				});
				self.toMain(self.account);
			});
		},
		toMain(userName) {
			this.login(userName);
			/**
			 * 强制登录时使用reLaunch方式跳转过来
			 * 返回首页也使用reLaunch方式
			 */
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../contents/contents'
				});
			} else {
				uni.navigateBack();
			}
		}
	}
};
